import React, { useCallback } from "react";
import { Dayjs } from "dayjs";
import { PanelWrapper } from "../DatePanel/styled";
import { Header } from "../Header";
import { MonthBody } from "./MonthBody";
import { DateController } from "../../date-util";
import type { IMonthPanelProps } from '../interface';

export const MonthPanel: React.FC<IMonthPanelProps> = React.memo((props) => {
  const { viewDate, onPanelChange, onSelect, showNow, nowNode, ...rest } = props;

  const handleSelect = useCallback(
    (value: Dayjs) => {
      onSelect(value);
      onPanelChange('month', "date");
    },
    [onSelect]
  );

  const onYearChange = useCallback((diff: number) => {
    const newDate = DateController.addYear(viewDate, diff);
    onSelect(newDate);
  }, [viewDate, onSelect]);

  return (
    <PanelWrapper showNow={showNow}>
      <Header onPrev={() => onYearChange(-1)} onNext={() => onYearChange(1)}>
        <span onClick={() => onPanelChange('month', "year")}>
          {DateController.getYear(viewDate)}
        </span>
      </Header>
      <MonthBody viewDate={viewDate} onSelect={handleSelect} {...rest} />
      {nowNode}
    </PanelWrapper>
  );
});
